























































































































































































































import Vue from 'vue';

export default Vue.extend({
    data() {
        return {
            drawer: null
        };
    },

    computed: {
        time() {
            return new Date().toTimeString().substr(0, 5);
        }
    },

    created() {
        this.$onSocket('open', () => {
            console.log('registering');
            this.$emitSocket('register', {
                username: this.$store.state.user.username
            });
        });
    },

    destroyed() {
        this.$emitSocket('unregister');
    }
});
